import React from "react";
import { Button } from "reactstrap";
import { getAccessTokenWithoutContext } from "../utils/auth0login";
import { getConfig } from "../config";
import APITokenManager from "../utils/apiTokenManager";

const AuthorizeUserButton = ({callback}) => {
    const config = getConfig();

    const authorizeUser = async () => {
      try {
        const tkn = await getAccessTokenWithoutContext(window.location.origin);
        const token = await APITokenManager.getAccessToken();
        const response = await fetch(`${config.apiOrigin}openid/register`, {
          method: 'POST',
          headers: {
            Authorization: `Bearer ${token}`,
            "content-type": 'application/json',
          },
          body: JSON.stringify({
            email: tkn.decodedToken.user.email,
            id_token: tkn.id_token,
          }),
        });
        
        const responseData = response.ok
          ? await response.json()
          : `Request failed with status ${response.status}`;
        
        callback(responseData);
      } catch (error) {
        callback(error);
      }
    };

    return (
      <Button
      color="primary"
      className="mt-5"
      onClick={authorizeUser}
      disabled={!config.audience}
      >
        Authorize User
      </Button>
    );
}

export default AuthorizeUserButton;