import {
    encode,
    bufferToBase64UrlEncoded,
    sha256,
    createRandomString,
    runPopup,
    openPopup,
    patchOpenUrlWithOnRedirect,
    parseAuthenticationResult,
    urlSafeEncode,
    // parseNumber,
} from "./auth0utils";
import { oauthToken } from "./auth0token";
import { verify } from "./auth0jwt";
import { getConfig } from "../config";
import { TransactionManager } from "./auth0transactionManager";
import { SessionStorage } from "./auth0storage";

const config = getConfig();

const stripUndefined = (params) => {
    return Object.keys(params)
        .filter(k => typeof params[k] !== 'undefined')
        .reduce((acc, key) => ({ ...acc, [key]: params[key] }), {});
};

const prepareAuthorizeUrl = async (redirect_uri, response_mode) => {
    
    const nonce = encode(createRandomString());
    const code_verifier = createRandomString();
    const code_challengeBuffer = await sha256(code_verifier);
    const code_challenge = bufferToBase64UrlEncoded(code_challengeBuffer);
    const auth0Client = encode(JSON.stringify({
        name: "auth0-spa-js",
        version: "2.0.4",
    }));
    const customState = {
        client_id: config.clientId,
        code_verifier,
        grant_type: "authorization_code",
        redirect_uri: config.proxyUrl,
        origin: redirect_uri,
    };
    const state = `${encode(createRandomString())}-${encode(JSON.stringify(customState))}`;

    const params = {
        client_id: config.clientId,
        scope: 'openid profile email',
        redirect_uri: config.proxyUrl,
        audience: config.audience,
        response_type: 'code',
        response_mode,
        state,
        nonce,
        code_challenge,
        code_challenge_method: 'S256',
        auth0Client,
    };

    const url = `https://${config.domain}/authorize?${new URLSearchParams(
        stripUndefined(params)
    )}`;

    return {
        nonce,
        code_verifier,
        scope: params.scope,
        audience: params.audience || 'default',
        redirect_uri: params.redirect_uri,
        state,
        url
    };
};

async function verifyIdToken(
    id_token,
    nonce,
    organizationId,
) {
    const now = Date.now();
    return verify({
      iss: `https://${config.domain}/`,
      aud: config.clientId,
      id_token,
      nonce,
      organizationId,
      leeway: 60,
    //   max_age: parseNumber(this.options.authorizationParams.max_age),
      now
    });
};

const requestToken = async (
    options,
    additionalParameters,
) => {
    const { nonceIn, organizationId } = additionalParameters || {};
    const authResult = await oauthToken(
        {
        baseUrl: config.domain,
        client_id: config.clientId,
        auth0Client: {
            name: 'auth0-spa-js',
            version: '2.0.4',
        },
        useFormData: true,
        timeout: 10000,
        ...options
        }
    );
    
    console.log("KOKO DAYO", JSON.stringify(authResult));

    const decodedToken = await verifyIdToken(
        authResult.id_token,
        nonceIn,
        organizationId
    );

    return { ...authResult, decodedToken };
};

export const getAccessTokenWithoutContext = async (redirect_uri) => {
    const loginConfig = {};

    loginConfig.popup = openPopup('');

    if (!loginConfig.popup) {
        throw new Error(
        'Unable to open a popup for loginWithPopup - window.open returned `null`'
        );
    }
    const params = await prepareAuthorizeUrl(redirect_uri, 'web_message');

    loginConfig.popup.location.href = params.url;

    const codeResult = await runPopup({
        ...loginConfig,
        timeoutInSeconds: 60, 
    });

    if (params.state !== codeResult.state) {
        throw new Error('Invalid state');
    }

    const token = await requestToken({ 
        audience: params.audience,
        scope: params.scope,
        code_verifier: params.code_verifier,
        grant_type: 'authorization_code',
        code: codeResult.code,
        redirect_uri: params.redirect_uri
    }, {
        nonceIn: params.nonce,
    });
    return token;
};

/**
 * ```js
 * await loginWithCustomStateRedirect(options);
 * ```
 *
 * Performs a redirect to `/authorize` using the parameters
 * provided as arguments. Random and secure `nonce`
 * parameters will be auto-generated. Overridden `state` parameter
 * containing custom fields will be forwarded to proxy for token retrieval
 *
 * @param options
 */
export const loginWithCustomStateRedirect = async (redirect_uri) => {
    const options = {};
    const { openUrl, fragment, appState, ...urlOptions } =
        patchOpenUrlWithOnRedirect(options);

    const organizationId =
        urlOptions.authorizationParams?.organization ||
        config.organization;

    const { url, ...transaction } = await prepareAuthorizeUrl(redirect_uri, 'query');

    transactionManager.create({
        ...transaction,
        appState,
        ...(organizationId && { organizationId })
    });

    const urlWithFragment = fragment ? `${url}#${fragment}` : url;

    if (openUrl) {
        await openUrl(urlWithFragment);
    } else {
        window.location.assign(urlWithFragment);
    }
}

/**
 * After the browser redirects back to the callback page,
 * call `handleRedirectCallback` to handle success and error
 * responses from Auth0. If the response is successful, results
 * will be valid according to their expiration times.
 */
export const handleRedirectCallback = async (url = window.location.href) => {
    console.log("handling");
    const queryStringFragments = url.split('?').slice(1);

    if (queryStringFragments.length === 0) {
        throw new Error('There are no query params available for parsing.');
    }

    const { state, code, error, error_description } = parseAuthenticationResult(
        queryStringFragments.join('')
    );

    const transaction = transactionManager.get();

    if (!transaction) {
        throw new Error('Invalid state');
    }

    transactionManager.remove();

    if (error) {
        throw new Error(
            error,
            error_description || error,
            state,
            transaction.appState
        );
    }

    // Transaction should have a `code_verifier` to do PKCE for CSRF protection
    if (
        !transaction.code_verifier ||
        (transaction.state && transaction.state !== state)
    ) {
        throw new Error('Invalid state');
    }

    const organizationId = transaction.organizationId;
    const nonceIn = transaction.nonce;
    const redirect_uri = transaction.redirect_uri;

    await requestToken(
        {
        audience: transaction.audience,
        scope: transaction.scope,
        code_verifier: transaction.code_verifier,
        grant_type: 'authorization_code',
        code: code.toString(),
        ...(redirect_uri ? { redirect_uri } : {})
        },
        { nonceIn, organizationId }
    );

    return {
        appState: transaction.appState
    };
}

const transactionManager = new TransactionManager(
    SessionStorage,
    "GSF",
);