import React, { useEffect } from 'react';
import { loginWithCustomStateRedirect } from '../utils/auth0login';

/**
 * @ignore
 */
const defaultOnRedirecting = () => <></>;

/**
 * @ignore
 */
const defaultReturnTo = () =>
  `${window.location.pathname}${window.location.search}`;

/**
 * ```js
 * const MyProtectedComponent = withAuthenticationRequired(MyComponent);
 * ```
 *
 * When you wrap your components in this Higher Order Component and an anonymous user visits your component
 * they will be redirected to the login page and returned to the page they we're redirected from after login.
 */
const withAuthRequired = (
  Component,
  options = {}
) => {
  return function WithAuthenticationRequired(props) {
    const {
      returnTo = defaultReturnTo,
      onRedirecting = defaultOnRedirecting,
      loginOptions,
      context,
    } = options;

    const { appUser, isLoading, appLogin } =
      React.useContext(context);

    useEffect(() => {
      if (isLoading || appUser) {
        return;
      }
      // Check localStorage/cache for user creds
      (async () => {
        await loginWithCustomStateRedirect(window.location.href);
      })();
    }, [
      isLoading,
      appLogin,
      appUser,
      loginOptions,
      returnTo,
    ]);

    return appUser ? <Component {...props} /> : onRedirecting();
  };
};

export default withAuthRequired;