class EventManager {
  constructor() {
      this.events = {};
      this.timers = {};
  }

  /**
   * ```js
   * addEvent(eventType, event, callback);
   * ```
   *
   * Adds registered event listener with associated eventType
   */
  addEvent(eventType, event, callback) {
    const listener = { event, callback };
    if (!Object.keys(this.events).includes(eventType)) {
      this.events[eventType] = new Set();
      console.log(`Creating new event handling key pair for ${event}`);
    }
    if (!this.events[eventType].has(listener)) {
      this.events[eventType].add(listener);
      window.addEventListener(event, callback);
      console.log(`Adding new event handling key pair for ${event}`);
    }
  }

  /**
   * ```js
   * removeEvent(eventType, event, callback);
   * ```
   *
   * Removes registered event listener
   */
  removeEvent(eventType, event, callback) {
    const listener = { event, callback };
    if (Object.keys(this.events).includes(eventType)) {
      if (this.events[eventType].has(listener)) {
        window.removeEventListener(event, callback);
        this.events[eventType].delete(listener);
        console.log(`Removing event handling key pair for ${event}`);
      };
    }
  }

  /**
   * ```js
   * removeEventsByType(eventType);
   * ```
   *
   * Removes all registered event listeners 
   * associated with eventType
   */
  removeEventsByType(eventType) {
    if (Object.keys(this.events).includes(eventType)) {
      const eventSet = this.events[eventType];
      for (let { event, callback } of eventSet) {
        window.removeEventListener(event, callback);
      }
      console.log(`Removing event handlers for ${eventType}`);
      delete this.events[eventType];
    }
  }

  /**
   * ```js
   * addTimer(eventType, event, callback);
   * ```
   *
   * Adds registered timer with associated eventType
   */
  addTimer(timerType, timer) {
    if (!Object.keys(this.timers).includes(timerType)) {
      this.timers[timerType] = new Set();
    }
    if (!this.timers[timerType].has(timer)) {
      this.timers[timerType].add(timer);
    }
  }

  /**
   * ```js
   * removeTimer(timerType, timer);
   * ```
   *
   * Removes registered timer
   */
  removeTimer(timerType, timer) {
    if (Object.keys(this.timers).includes(timer)) {
      if (this.timers[timerType].has(timer)) {
        clearTimeout(timer);
        clearInterval(timer);
        this.timers[timerType].delete(timer);
      };
    }
  }

  /**
   * ```js
   * removeTimersByType(timerType);
   * ```
   *
   * Removes all registered timers 
   * associated with timerType
   */
  removeTimersByType(timerType) {
    if (Object.keys(this.timers).includes(timerType)) {
      const timerSet = this.timers[timerType];
      for (let timer of timerSet) {
        clearTimeout(timer);
        clearInterval(timer);
      }
      delete this.timers[timerType];
    }
  }
}

export const eventManager = new EventManager();

  