import React, { useCallback, useEffect } from "react";
import { Router, Route, Switch } from "react-router-dom";
import { Container } from "reactstrap";

import NavBar from "./components/NavBar";
import Footer from "./components/Footer";
import AuthContext from "./components/AuthContext";
import Home from "./views/Home";
import Profile from "./views/Profile";
import ExternalApi from "./views/ExternalApi";
import history from "./utils/history";

// styles
import "./App.css";

// fontawesome
import initFontAwesome from "./utils/initFontAwesome";
import APITokenManager from "./utils/apiTokenManager";
import APIService from "./utils/apiService";
import { decode } from "./utils/auth0jwt";
import { disposeEvents as disposeIdlers, handleIdle } from "./utils/handleIdle";
import { handleRedirectCallback } from "./utils/auth0login";
import { hasAuthParams } from "./utils/auth0utils";
import { eventManager } from "./utils/eventsManager";
initFontAwesome();

const App = () => {

  const [appUser, setUser] = React.useState(null);
  const [isLoggedIn, setIsLoggedIn] = React.useState(false);

  const appLogin = useCallback((response) => {
    setUser(response);
    setIsLoggedIn(true);
  }, []);

  const appLogout = useCallback(() => {
    console.log("logoutapp");
    disposeIdlers();
    setUser(null);
    setIsLoggedIn(false);
  }, []);

  // wrapping in callback mutates function reference and causes
  // remove event listener to not function correctly
  const logOutFn = useCallback(() => {
    APIService.logout();
    appLogout();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const onRedirectCallback = useCallback((appState) => {
    console.log("triggered");
    history.push(
      appState && appState.returnTo ? appState.returnTo : window.location.pathname
    );
  }, []);

  useEffect(() => {
    // check cache for valid refresh token
    const refreshToken = APITokenManager.getRefreshToken();
    console.log(refreshToken);
    if (refreshToken) {
      // Obtain access token and set user
      APITokenManager.getAccessToken().then(res => {
        if (res !== "") {
          const decoded = decode(res);
          setUser(decoded.user);
          console.log(decoded.user);
        }
      });
    }
  }, []);

  const getAT = async () => {
    const token = await APITokenManager.getAccessToken();
    console.log("AT: ", token);
  };

  const getRT = () => APITokenManager.getRefreshToken();

  useEffect(() => {
    getRT();
    getAT();
    eventManager.addEvent("sso", "mousedown", getAT);
    eventManager.addEvent("sso", "mousedown", getRT);
    return () => {
      eventManager.removeEventsByType("sso");
    };
  }, []);

  // logout if idle
  useEffect(() => {
    if (appUser) {
      handleIdle(logOutFn);
    }
  }, [appUser, logOutFn]);


  // handle redirect from Auth0 proxy
  useEffect(() => {
    (async () => {
      try {
        let user;
        if (hasAuthParams()) {
          const { appState } = await handleRedirectCallback();
          onRedirectCallback(appState, user);
        }
      } catch (error) {
        console.error(error);
      }
    })();
  }, [onRedirectCallback]);

  // useEffect(() => {
  //   Hub.listen('auth', ({ payload: { event, data } }) => {
  //     switch (event) {
  //       case 'signIn':
  //       case 'cognitoHostedUI':
  //         console.log('Authenticated...');
  //         console.log(event);
  //         console.log(data);
  //         console.log(Amplify);
  //         Auth.currentAuthenticatedUser().then(res => {
  //           console.log(res);
  //           setUser(res);
  //         })
  //         break;
  //       case 'signIn_failure':
  //       case 'cognitoHostedUI_failure':
  //         console.log('Error', data);
  //         break;
  //       default:
  //     }
  //   });
  // }, []);

  // // Configure Amplify in index file or root file
  // const awsConfigs = {
  //   Auth: {
  //     region: awsExports.REGION,
  //     userPoolId: awsExports.USER_POOL_ID,
  //     userPoolWebClientId: awsExports.USER_POOL_APP_CLIENT_ID,
  //     oauth: {
  //       domain: awsExports.DOMAIN,
  //       scope: ["email", "openid", "profile"],
  //       redirectSignIn: awsExports.REDIRECT_SIGN_IN,
  //       redirectSignOut: awsExports.REDIRECT_SIGN_OUT,
  //       responseType: awsExports.RESPONSE_TYPE,
  //     },
  //   },
  // };

  // // On redirect back to application, configure() runs and completes token flow if code parameter present.
  // if (window.location.search) awsConfigs.Auth.oauth.redirectSignIn = window.location.href;
  
  // // Override default configure behaviour from sdk since does not comply with architecture
  // // SDK assumes two-legged comm between app and aws
  // // Our architecture uses auth proxy in AWS
  // configure(Auth, awsConfigs);
  // Amplify.configure(awsConfigs);

  // // Auth.configure(awsConfigs);

  // if (window.location.search) {
  //   Auth._config.Auth.oauth.redirectSignIn = awsExports.REDIRECT_SIGN_IN;
  // }

  // if (isLoading) {
  //   return <Loading />;
  // }

  return (
    <AuthContext.Provider value={{ appUser, appLogin, appLogout, isLoggedIn }}>
      <Router history={history}>
        <div id="app" className="d-flex flex-column h-100">
          <NavBar />
          <Container className="flex-grow-1 mt-5">
            <Switch>
              <Route path="/" exact component={Home} />
              <Route path="/profile" component={Profile} />
              <Route path="/external-api" component={ExternalApi} />
            </Switch>
          </Container>
        </div>
      </Router>
    </AuthContext.Provider>
  );
};

export default App;
