import {
    MfaRequiredError,
    MissingRefreshTokenError,
    GenericError,
} from '@auth0/auth0-spa-js';
import { createQueryParams } from './auth0utils';

const createAbortController = () => new AbortController();

const dofetch = async (fetchUrl, fetchOptions) => {
    const response = await fetch(fetchUrl, fetchOptions);
  
    return {
      ok: response.ok,
      json: await response.json()
    };
};

const fetchWithoutWorker = async (
    fetchUrl,
    fetchOptions,
    timeout,
) => {
    const controller = createAbortController();
    fetchOptions.signal = controller.signal;
  
    let timeoutId;
  
    // The promise will resolve with one of these two promises (the fetch or the timeout), whichever completes first.
    return Promise.race([
      dofetch(fetchUrl, fetchOptions),
  
      new Promise((_, reject) => {
        timeoutId = setTimeout(() => {
          controller.abort();
          reject(new Error("Timeout when executing 'fetch'"));
        }, timeout);
      })
    ]).finally(() => {
      clearTimeout(timeoutId);
    });
};

async function getJSON(
    url,
    timeout,
    audience,
    scope,
    options,
) {
    let fetchError;
    let response;
    let tokenRetryCount = 3;
  
    for (let i = 0; i < tokenRetryCount; i++) {
      try {
        response = await fetchWithoutWorker(
          url,
          options,
          timeout
        );
        fetchError = null;
        break;
      } catch (e) {
        // Fetch only fails in the case of a network issue, so should be
        // retried here. Failure status (4xx, 5xx, etc) return a resolved Promise
        // with the failure in the body.
        // https://developer.mozilla.org/en-US/docs/Web/API/Fetch_API
        fetchError = e;
      }
    }
  
    if (fetchError) {
      throw fetchError;
    }
  
    const {
      json: { error, error_description, ...data },
      ok
    } = response;
  
    if (!ok) {
      const errorMessage =
        error_description || `HTTP error. Unable to fetch ${url}`;
  
      if (error === 'mfa_required') {
        throw new MfaRequiredError(error, errorMessage, data.mfa_token);
      }
  
      if (error === 'missing_refresh_token') {
        throw new MissingRefreshTokenError(audience, scope);
      }
  
      throw new GenericError(error || 'request_error', errorMessage);
    }
  
    return data;
}

export async function oauthToken({
    baseUrl,
    timeout,
    audience,
    scope,
    auth0Client,
    useFormData,
    ...options
}) {
  const body = useFormData
    ? createQueryParams(options)
    : JSON.stringify(options);

  return await getJSON(
    `https://${baseUrl}/oauth/token`,
    timeout,
    audience || 'default',
    scope,
    {
      method: 'POST',
      body,
      headers: {
        'Content-Type': useFormData
          ? 'application/x-www-form-urlencoded'
          : 'application/json',
        'Auth0-Client': btoa(
          JSON.stringify({
            name: 'auth0-spa-js',
            version: '2.0.4',
          })
        )
      }
    },
    useFormData
  );
}