import { eventManager } from "./eventsManager";

const TimeInMs = {
    Second: 1000,
    Minute: 60 * 1000,
    Hour: 60 * 60 * 1000,
};

const softLogoutIdleDurationInMS = 3 * TimeInMs.Minute;
const hardLogoutIdleDurationInMS = 15 * TimeInMs.Minute;

let count, counter, timer, hardTimer;

export const disposeEvents = () => {
    eventManager.removeTimersByType("softTimeout");
    eventManager.removeTimersByType("hardTimeout");
    eventManager.removeTimersByType("secondsCounter");
    count = 0;
    eventManager.removeEventsByType('idleTimer');
    eventManager.removeEventsByType('idleLogout');
}

export const handleIdle = (logout) => {
    const logoutOnNextAction = () => {
        console.log('out');
        eventManager.addEvent('idleLogout', 'click', logout);
        eventManager.addEvent('idleLogout', 'keydown', logout);
    }

    const forcedLogout = () => {
        console.log('forced');
        logout();
    }

    count = 0;

    const counting = () => {
        count++;
        console.log(`${count} seconds idling...`);
    };

    counter = setInterval(counting, TimeInMs.Second);
    timer = setTimeout(logoutOnNextAction, softLogoutIdleDurationInMS);
    hardTimer = setTimeout(forcedLogout, hardLogoutIdleDurationInMS);
    eventManager.addTimer("softTimeout", timer);
    eventManager.addTimer("hardTimeout", hardTimer);
    eventManager.addTimer("secondsCounter", counter);

    const resetIdleTimers = () => {
        console.log('reset idle');
        eventManager.removeTimersByType("softTimeout");
        eventManager.removeTimersByType("hardTimeout");
        eventManager.removeTimersByType("secondsCounter");
        count = 0;
        timer = setTimeout(logoutOnNextAction, softLogoutIdleDurationInMS);
        hardTimer = setTimeout(forcedLogout, hardLogoutIdleDurationInMS);
        counter = setInterval(counting, TimeInMs.Second);
        eventManager.addTimer("softTimeout", timer);
        eventManager.addTimer("hardTimeout", hardTimer);
        eventManager.addTimer("secondsCounter", counter);
    };

    eventManager.addEvent('idleTimer', 'mousemove', resetIdleTimers);
    eventManager.addEvent('idleTimer', 'keydown', resetIdleTimers);

    return () => {
        eventManager.removeEventsByType("softTimeout");
        eventManager.removeEventsByType("hardTimeout");
        eventManager.removeTimersByType("secondsCounter");
        eventManager.removeEventsByType('idleTimer');
        eventManager.removeEventsByType('idleLogout');
    };
}