import { decode } from "./auth0utils";
import { getConfig } from "../config";

const { apiOrigin } = getConfig();

let accessToken = "";

let expiry = 0;

const getAccessToken = async () => {
    const now = Math.floor(Date.now() / 1000);
    console.log(`Obtaining access token at ${now}`);
    console.log(expiry);
    console.log("remaining", expiry - now);
    const rt = getRefreshToken();
    console.log("refresh", rt);
    // refresh if 5 seconds before expiring
    if (rt !== "" && expiry - now < 5) {
        // acquire new access token
        console.log("requesting...");
        const response = await fetch(`${apiOrigin}auth/token`, {
            method: "POST",
            body: JSON.stringify({
                refresh_token: rt,
            }),
            headers: {
                "Content-Type": "application/json",
            },
        })
        if (response.ok) {
            const json = await response.json();
            setAccessToken(json.access_token);
            setRefreshToken(json.refresh_token, json.expires_in);
        }
        else {
            return "";
        }
    }
    const end = Math.floor(Date.now() / 1000);
    console.log(`Get access token finished at ${end}`);
    return accessToken;
}

const setAccessToken = (token) => {
    accessToken = token;
    const decoded = JSON.parse(decode(token.split('.')[1]));
    expiry = decoded.exp;
    console.log(decoded.exp);
}

const setRefreshToken = (token, exp) => {
    const now = Math.floor(Date.now() / 1000);
    exp += now;
    localStorage.setItem("rt", JSON.stringify({
        token,
        exp,
    }));
};

const setRefreshMutex = () => {
    if (typeof localStorage === 'undefined') return "";
}

const getRefreshToken = () => {
    if (typeof localStorage === 'undefined') return "";
    
    const val = localStorage.getItem("rt");

    if (val == null) return "";
    
    const { token, exp } = JSON.parse(val);

    // check token expired
    const now = Math.floor(Date.now() / 1000);
    console.log(now, exp);
    if (now >= exp - 5) return "";
    
    return token;
}

const clearTokens = () => {
    localStorage.removeItem("rt");
    accessToken = "";
    expiry = 0;
}

accessToken = getAccessToken();


const APITokenManager = {
    getAccessToken,
    setAccessToken,
    getRefreshToken,
    setRefreshToken,
    clearTokens,
}

export default APITokenManager;