import React, { useState } from "react";
import { Button, Alert } from "reactstrap";
import Highlight from "../components/Highlight";
import { useAuth0 } from "@auth0/auth0-react";
import { getConfig } from "../config";
import AuthorizeUserButton from "../components/AuthorizeUserButton"
import APITokenManager from "../utils/apiTokenManager";
import APIService from "../utils/apiService";
import withAuthRequired from "../components/withAuthRequired";
import AuthContext from "../components/AuthContext";

export const ExternalApiComponent = () => {
  const { apiOrigin, audience } = getConfig();

  const {
    getAccessTokenSilently,
    loginWithPopup,
    getAccessTokenWithPopup,
  } = useAuth0();

  const [state, setState] = useState({
    showResult: false,
    apiMessage: "",
    error: null,
  });

  const handleConsent = async () => {
    try {
      await getAccessTokenWithPopup();
      setState({
        ...state,
        error: null,
      });
    } catch (error) {
      setState({
        ...state,
        error: error.error,
      });
    }

    await callApi();
  };

  const handleLoginAgain = async () => {
    try {
      await loginWithPopup();
      setState({
        ...state,
        error: null,
      });
    } catch (error) {
      setState({
        ...state,
        error: error.error,
      });
    }

    await callApi();
  };

  const callApi = async () => {
    try {
      const response = await APIService.authorizedGet("WeatherForecast");

      const responseData = await response.json();

      setState({
        ...state,
        showResult: true,
        apiMessage: responseData,
      });
    } catch (error) {
      setState({
        ...state,
        error: error.error,
      });
    }
  };

  const callProtectedApi = async () => {
    try {
      const token = await APITokenManager.getAccessToken();

      console.log(token);
      const response = await fetch(`${apiOrigin}WeatherForecast/5`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      
      const responseData = response.ok
        ? await response.json()
        : `Request failed with status ${response.status}`;

      setState({
        ...state,
        showResult: true,
        apiMessage: responseData,
      });
    } catch (error) {
      setState({
        ...state,
        showResult: true,
        apiMessage: error,
      });
    }
  };
  
  const displayAccessToken = async () => {
    try {
      const token = await getAccessTokenSilently();
      setState({
        ...state,
        showResult: true,
        apiMessage: token,
      });
    } catch (error) {
      setState({
        ...state,
        showResult: true,
        apiMessage: error,
      })
    }
  }


  const handle = (e, fn) => {
    e.preventDefault();
    fn();
  };

  return (
    <>
      <div className="mb-5">
        {state.error === "consent_required" && (
          <Alert color="warning">
            You need to{" "}
            <a
              href="#/"
              class="alert-link"
              onClick={(e) => handle(e, handleConsent)}
            >
              consent to get access to users api
            </a>
          </Alert>
        )}

        {state.error === "login_required" && (
          <Alert color="warning">
            You need to{" "}
            <a
              href="#/"
              class="alert-link"
              onClick={(e) => handle(e, handleLoginAgain)}
            >
              log in again
            </a>
          </Alert>
        )}

        <h1>External API</h1>
        <p className="lead">
          Ping an external API by clicking the button below.
        </p>

        <p>
          This will call a local API on port 3001 that would have been started
          if you run <code>npm run dev</code>. An access token is sent as part
          of the request's `Authorization` header and the API will validate it
          using the API's audience value.
        </p>

        {!audience && (
          <Alert color="warning">
            <p>
              You can't call the API at the moment because your application does
              not have any configuration for <code>audience</code>, or it is
              using the default value of <code>YOUR_API_IDENTIFIER</code>. You
              might get this default value if you used the "Download Sample"
              feature of{" "}
              <a href="https://auth0.com/docs/quickstart/spa/react">
                the quickstart guide
              </a>
              , but have not set an API up in your Auth0 Tenant. You can find
              out more information on{" "}
              <a href="https://auth0.com/docs/api">setting up APIs</a> in the
              Auth0 Docs.
            </p>
            <p>
              The audience is the identifier of the API that you want to call
              (see{" "}
              <a href="https://auth0.com/docs/get-started/dashboard/tenant-settings#api-authorization-settings">
                API Authorization Settings
              </a>{" "}
              for more info).
            </p>

            <p>
              In this sample, you can configure the audience in a couple of
              ways:
            </p>
            <ul>
              <li>
                in the <code>src/index.js</code> file
              </li>
              <li>
                by specifying it in the <code>auth_config.json</code> file (see
                the <code>auth_config.json.example</code> file for an example of
                where it should go)
              </li>
            </ul>
            <p>
              Once you have configured the value for <code>audience</code>,
              please restart the app and try to use the "Ping API" button below.
            </p>
          </Alert>
        )}
        <div className="flex-start">
          <Button
            color="primary"
            className="mt-5"
            onClick={() => {
              setState({
                ...state,
                showResult: false,
              });
              callApi();
            }}
            disabled={!audience}
          >
            Ping API
          </Button>
          <Button
            color="primary"
            className="mt-5"
            onClick={() => {
              setState({
                ...state,
                showResult: false,
              });
              callProtectedApi();
            }}
            disabled={!audience}
          >
            Ping Protected API
          </Button>
          {/* {showAuthorize && */}
            <AuthorizeUserButton
              callback={(msg) => setState({
                ...state,
                showResult: true,
                apiMessage: msg,
              })}
            />
          {/* } */}
          <Button
            color="primary"
            className="mt-5"
            onClick={() => {
              setState({
                ...state,
                showResult: false,
              });
              displayAccessToken();
            }}
            disabled={!audience}
          >
            Show Access Token
          </Button>
        </div>
      </div>

      <div className="result-block-container">
        {state.showResult && (
          <div className="result-block" data-testid="api-result">
            <h6 className="muted">Result</h6>
            <Highlight>
              <span>{JSON.stringify(state.apiMessage, null, 2)}</span>
            </Highlight>
          </div>
        )}
      </div>
    </>
  );
};

export default withAuthRequired(ExternalApiComponent, {
  context: AuthContext,
});
