// Wrapper class to automate token management when calling API
import APITokenManager from './apiTokenManager';
import { getConfig } from '../config';
import { decode } from './auth0jwt';

const baseUrl = getConfig().apiOrigin;

const getHeaders = async () => {
    const token = await APITokenManager.getAccessToken();
    return {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
    }
};

const openIDLogin = async (id_token) => {
    return await login(`${baseUrl}openid/login`, {
        id_token,
    }).catch((err) => {
        console.log("err", err);
        throw err;
    });
};

const offlineLogin = async (email, password) => {
    return await login(`${baseUrl}login`, {
        email,
        password,
    }).catch((err) => {
        throw err;
    });
};

const login = async (url, body) => {
    const response = await fetch(url, {
        method: "POST",
        body: JSON.stringify(body),
        headers: {
            "Content-Type": "application/json",
        },
    });
    
    if (response.ok) {
        const json = await response.json();
        
        if (json.access_token && json.refresh_token && json.expires_in) {
            APITokenManager.setAccessToken(json.access_token);
            APITokenManager.setRefreshToken(json.refresh_token, json.expires_in);
        }
        console.log(json.access_token);
        return decode(json.access_token);
    }
    else {
        return response;
    }
};

const logout = async () => {
    const headers = await getHeaders();
    APITokenManager.clearTokens();
    fetch(`${baseUrl}logout`, {
        method: "POST",
        headers,
    });
}

const authorizedGet = async (url) => {
    const headers = await getHeaders();
    return fetch(baseUrl + url, {
        method: "GET",
        headers,
    });
};

const APIService = {
    offlineLogin,
    openIDLogin,
    logout,
    authorizedGet,
};

export default APIService;